body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.chat {
  list-style-type: none;
  width: 20em;
}

.chat__bubble {
  margin-bottom: 3px;
  clear: both;
  border-radius: 10px 10px 2px 2px ;
  border: 1px solid #f2f2f2;
}

.chat__bubble .text {
  padding: 5px 10px;
}

.chat__bubble--rcvd {
  background: #f2f2f2;
  color: black;
  float: left;
  border-bottom-right-radius: 10px;
}

.chat_button {
  background-color: white;
text-align: center;
color: #0084ff;
padding: 10px;
border: 1px solid #f2f2f2;
}

.chat__bubble--sent {
  background: #0066ff;
  border-color: #0066ff;
  color: white;
  float: right;
  border-bottom-left-radius: 10px;
}

.chat__bubble--sent + .chat__bubble--sent {
  border-top-right-radius: 2px;
}

.chat__bubble--rcvd + .chat__bubble--rcvd {
  border-top-left-radius: 2px;
}

.chat__bubble--stop {
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}



